.article{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: var(--color-footer);
}

.article_img{
width: 100%;
height: 100%;
background: var(--color-footer);
}
.article_img img{
    width: 100%;
    height: 100%;
}

.article_content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}

.article_content p{
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 11.6px;
    line-height: 35px;
    color: white;
}

.article_content h3{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 25.11px;
    line-height: 30px;
    color: white;
    cursor: pointer;
    margin-bottom: 5rem;
}

.article_content p:last-child{
    cursor: pointer;
}
.article_content p:last-child:hover{
    color: aqua;
}

@media screen and (max-width:550px) {
    .article_content h3{
        font-size: 18px;
        line-height: 25px;
    }
}