.nav{
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}

.links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

}

.logo{
    margin-right: 2rem;
}

.logo img{
    width: 80px;
   height: 80px;
}

.text{
    display: flex;
    flex-direction: row;
    color:#fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: auto;
    line-height: auto;
    text-transform: capitalize;

}

.text p,.sign p {
    margin: 0 1rem;
    text-align: center;
    cursor: pointer;

}
.sign{
    color: white;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.sign button{
padding: 0rem 1rem;
color: white;
   font-weight: 500;
   border: 0;
   outline: none;
 font-family: var(--font-family);
    background: orange;
    border-radius:5px ;
    line-height: 25px;
}
.menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}
.menu svg{
    cursor: pointer;
}

.container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction:column ;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem ;
    position: absolute;
    color: #fff;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0 , 0, 0.2);
}
.container p {
    margin: 1rem 0;
}

.navbar-sign{
      color: white;
    display: none;
}
.navbar-sign button{
    padding: 0rem 1rem;
color: white;
   font-weight: 500;
   border: 0;
   outline: none;
 font-family: var(--font-family);
    background: orange;
    border-radius:5px ;
    line-height: 25px;
}

@media screen and (max-width:1050px){
    .text{
        display: none;
    }

    .menu{
        display: flex;
    }
}
@media screen and (max-width:700px){
    .nav{
        padding: 2rem 4rem;
    }

  
}
@media screen and (max-width:550px){
    .nav{
        padding: 2rem;
    }
    .sign{
        display: none;
    }
    .container{
        top: 20px;
    }
    .navbar-sign{
        display: block;
    }
}