.feature2{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;

}

.feature2_heading{
flex:1;
display: flex;
justify-content: flex-start;
align-items: flex-start;
flex-direction: column;
text-align: left;
margin-right: 5rem;
}

.feature2_heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}

.feature2_heading p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #FF8A71;
    margin-top: 2rem;
}

.feature2_container{
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width:990px){
    .feature2{
        flex-direction: column;
    }
    .feature2_heading{
        margin: 0 0 2rem 0;
    }
}

@media screen and (max-width:550px){
   
    .feature2_heading h1{
        font-size: 20px;
        line-height: 38px;

    }
}