.possibility{
    display: flex;
    flex-direction: row;

}
.possibility_image{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}
.possibility_image img{
    width: 100%;
    height: 100%;
}

.possibility_content{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;

}

.possibility_content h4{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: aqua;
}
.possibility_content h4:last-of-type{
    color: #FF8A71;
}

.possibility_content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
}
.possibility_content p{
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
    margin-bottom: 2rem;
}

@media screen and (max-width:950px) {
    .possibility{
        flex-direction: column;
    }
    .possibility_image{
        margin: 1rem 0;
    }
    .possibility_content{
        margin-top: 2rem;
    }
}