.blog{
    display: flex;
    flex-direction: column;
}

.blog_heading{
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.blog_heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
    margin-bottom: 1rem;
}
.blog_content{
    display: flex;
    flex-direction: row;
    
}
.blog_content_groupa{
     flex: 0.75;
     margin-right: 2rem;
   
}
.blog_content_groupb{
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 2rem;
    
}

@media screen and (max-width:990px){
    .blog_content{
        flex-direction: column-reverse;

    }
    .blog_content_groupa{
        margin: 2rem 0;
    }
    .blog_content_groupa .blog_content_groupb{
        width: 48%;
    }
    .blog_content_groupa .blog_content_groupb{
        height: 250px;
    }
}

@media screen and (max-width:700px){
    .blog_content_groupb{
        grid-template-columns: repeat(1,1fr);
    }
    .blog_content_groupa .blog_content_groupb{
        width: 100%;

    }
    .blog_heading h1{
font-size: 46px;
line-height: 52px;
    }
}

@media screen and (max-width:550px){
    .blog_heading h1{
        font-size: 34px;
        line-height: 42px;
    }
    
}
