@media screen and (max-width:700px){
  .section_padding{
    padding: 4rem;
  }
  .section_margin{
    margin: 4rem;
  }
}
@media screen and (max-width:700px){
  .section_padding{
    padding: 4rem 2rem;
  }
  .section_margin{
    margin: 4rem 2rem;
  }
}