.container_feature{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}
.container_feature_title{
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
}

.container_feature_title h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: white;
}

.container_feature_title div{
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow:  0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}
 .container_feature-text{
     flex: 2;
     max-width: 390px;
     display: flex;
 }
 .container_feature-text p{
     font-family: var(--font-family);
     font-weight: 500;
     font-size: 14px;
     line-height: 24px;
     color: var(--color-text);
 }

 @media  screen and (max-width:550px) {
     .container_feature_title h1{
         font-size: 14px;
         line-height: 22px;

     }
      .container_feature_title p{
         font-size: 12px;
         line-height: 20px;
         
     }
     .container_feature{
         margin: 1rem 0;
     }
 }
