.footer{
    border: 1px solid white;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}

.footer_heading{
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.footer_heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    text-align: center;
    letter-spacing: -0.04em;
}

.footer_btn p{
    border: 1px solid white;
    box-sizing: border-box;
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 21px;
    color: white;
    word-spacing: 2px;
    padding: 10px;
}
.footer_btn{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1rem;
border: 1px solid white;
text-align: center;
margin-bottom: 10rem;
cursor: pointer;




}

.footer_links{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
}

.footer_links div{
    width: 150px;
    margin: 1rem;
}

.footer_links_logo{
    display: flex;
    flex-direction: column;

}

.footer_links_logo img{
    width: 118px;
    height: 110px;
    margin-bottom: 1rem;
}

.footer_links_logo p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: white;
}

.footer_links_div{
display: flex;
justify-content: flex-start;
flex-direction: column;

}
.footer_links_div  h4{
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
    color: white;
    margin-bottom:1rem ;
}

.footer_links_div p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: white;
    margin: 0.5rem 0;
    cursor: pointer;
}
.footer_copyright{
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.footer_copyright p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: white;
    letter-spacing: 2px;
}

@media screen and (max-width:850px){
    .footer_heading h1{
        font-size: 44px;
        line-height: 50px;

    }
}

@media screen  and (max-width:550px){
    .footer_heading h1{
        font-size: 34px;
        line-height: 40px;
    }

    .footer_links div{
        margin: 1rem 0;
    }

    .footer_btn p{
   font-size: 14px;
   line-height:20px;
    }
}

@media screen and (max-width:400px) {
    .footer_heading h1{
        font-size: 27px;
        line-height: 38px;
    }
    
}