.cta{
    border: 1px solid white;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.cta_content p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
    color: #0E0E0E;
}
.cta_content h3{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
    color: #000000;
}

.cta_button{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.cta_button button{
    background: #000000;
    color: white;
    border-radius: 40px;
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: bold;
    line-height: 30px;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

@media screen and (max-width:650px) {
    .cta{
        flex-direction: column;
    }
    .cta_button{
        margin: 2rem 0 0 ;
    }

}

@media screen and (max-width:550px) {
    .cta{
        margin: 4rem 2rem;
    
    }
    .cta_content h3{
        font-size: 18px;
        line-height: 32px;
    }
    .cta_button button{
        font-size: 14px;
        line-height: 28px;
    }
}