.header{
    display: flex;

}
.content{
    flex: 3;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction:column ;
    margin-right: 5rem;
}

.content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04rem;
}
.content p{
    font-family:var(--font-family);
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top: 1.2rem;
}

.input{
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}

.input input{
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    background-color: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    font-size: 20px;
    line-height: 27px;
    color: #fff;
   border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
}
.input button{
    flex: 0.2;
    width: 100%;
    min-height: 50px;
    font-family:'Times New Roman', Times, serif;
  border: none;
    font-size: 20px;
    line-height: 27px;
    background:#FF4820 ;
    color: white;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
     border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
}
.people{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}
.people img{
    width:100px;
    height: 100px;
}

.people p{
    font-family: var(--font-family);
    margin:0 0 0 3rem;
    font-weight: 500;
    font-size: 15px;
    line-height: 38px;
    color: #fff;
    text-align: center;
}

.image {
    flex: 2;
    display: flex;
    justify-content:center ;
    align-items: center;
    margin-top: -100px;


}
.image img{
    width: 100%;
    height: 100%;
    
}

@media screen and (max-width:1050px) {
    .header{
        flex-direction: column;
    }
    .content{
    margin:  0 0 3rem;
    }
}

@media  screen and (max-width:650px) {
    .header h1{
        font-size: 48px;
        line-height: 60px;

    }
    .header p{
        font-size: 16px;
        line-height: 24px;
    }
    .people{
        flex-direction: column;
    }
    .people p{
        margin: 0;
    }
    .input input,.input button{
     font-size: 16px;
     line-height: 24px;
    }
}

@media  screen and (max-width:490px){
    .header h1{
        font-size: 36px;
        line-height: 48px;

    }

    .header p{
        font-size: 14px;
        line-height: 24px;
    }
    .input input,.input button{
        font-size: 12px;
        line-height: 16px;
    }
}